body {
    margin: 0;
}

.container {
    max-width: 1440px;
    width: 100%;

    margin: 0 auto;

}

.header {
    max-width: 1440px;
    width: 100%;
    height: 146px;

    display: flex;
    align-items: center;
    margin-left: 26px;
}

.general_box {
    max-width: 1046px;
    width: 100%;
    height: 59px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;

}

.nav_bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 808px;
    width: 100%;
    height: 59px;
}

.left_nav_bar,
.right_nav_bar {
    max-width: 305px;
    width: 100%;
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}


.left_nav_bar>ul,
.right_nav_bar>ul {
    display: flex;
    list-style: none;
    width: inherit;
    height: inherit;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}

.ul-item {
    text-decoration: none;
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 27px;
    color: #000000;
}

.ul-item:hover {
    color: #C08D5D
}


.main_container {
    background-color: #FFFAF3;
    max-width: 1320px;
    width: 100%;
    margin: 40px 94px 132px 26px;
    box-shadow: 10px 70px 50px #FFFAF3;
   
}

.inner_container {
    width: 1320px;



    height: 472px;
    margin: 81px 665px 81px 120px;
}



.inner_paragraph {
    color: #3F4042;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 147.6%;
    font-family: 'Avenir';
    letter-spacing: 0.06em;
    width: 535px;
    height: 108px;
}

.bottom_button {
    display: flex;
    cursor: pointer;
    width: 197px;
    height: 64px;
}

.inner_button {
    width: 138px;
    height: 64px;
    border: 2px solid #C08D5D;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: 'Avenir';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 147.6%;
    text-decoration: none;

    letter-spacing: 0.06em;

    color: #3F4042;
}

.inner_button_right_part {
    margin: 0;
    padding: 0;
    width: 59px;
    height: 64px;
    background-color: #C08D5D;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grape {
    position: absolute;
    top: 0px;
    left: 800px;
    z-index: 0;
}

.foodFarming {


    font-family: 'Playfair Display';
    font-style: normal;
    font-weight: 700;
    font-size: 70px;
    line-height: 93px;


    color: #3F4042;
}

.foodAndPicture {
    display: flex;
}

.inner_underline {
    width: 165px;
    height: 9px;
}

.foodAndPicture h4 {
    margin: 0;
}

.foodFarming h1 {
    margin: 0;
}

.bottom_picture {
    display: flex;
    width: 197px;
    height: 64px;
    justify-content: space-between;
    margin-top: 25px;

}

.bottom_picture img {
    cursor: pointer;
}