@font-face {
    font-family: 'Avenir';
    src: local('Avenir Book'), local('Avenir-Book'),
        url('Avenir-Book.eot') format('eot'),
        url('Avenir-Book.woff2') format('woff2'),
        url('Avenir-Book.woff') format('woff'),
        url('Avenir-Book.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}